import React, { useEffect, useState } from "react";
import { menu } from "../ultis/menu";
import { BrowserRouter as Router, Link, useLocation } from "react-router-dom";
export default function Sidebar(props) {
  const location = useLocation();

  const urlParams = new URLSearchParams(window.location.search);
  const [key, setKey] = useState(urlParams.get("key") || "");

  console.log(key)
  useEffect(() => {
    const _queryString = window.location.search;
    const _urlParams = new URLSearchParams(_queryString);

    var currentKey = _urlParams.get("key");
    console.log(currentKey)

    if (currentKey !== key) {
      setKey(currentKey);
    }
  });

  function menuLink_3(link) {
    var result = null;
    if (link.length > 0) {
      result = link.map((link, index) => {
        var name = link.name;
        var active = "active-col";
        return (
          <Link
            className={`collapse-item  ${active}`}
            to={link.to + `?key=${link.key}`}
          >
            {name}
          </Link>
        );
      });
    }
    return result;
  }

  function menuLink_2(link) {
    var result = null;
    if (link?.length > 0) {
      result = link.map((link, index) => {
        if (typeof link.children !== "undefined") {
          const isActive = key === link.key;
          var active = isActive ? "active" : "";

          return (
            <React.Fragment>
              <li className={`nav-item ${active}`} key={index}>
                <a
                  className={`nav-link collapsed ${link.open}-collapse`}
                  href="#"
                  data-toggle="collapse"
                  data-target={`#collapse_${index}`}
                  aria-expanded="false"
                  aria-controls="collapseTwo"
                  style={{ padding: "0.75rem" }}
                >
                  <i className={link.icon}></i>
                  <span>{link.name}</span>
                </a>
                <div
                  id={`collapse_${index}`}
                  className="collapse"
                  aria-labelledby="headingTwo"
                  data-parent="#accordionSidebar"
                >
                  <div className="bg-white py-2 collapse-inner rounded">
                    <h6 className="collapse-header">{link.name}</h6>
                    {menuLink_3(link.children)}
                  </div>
                </div>
              </li>
            </React.Fragment>
          );
        }
        const isActive = key === link.key || (key === null && link.key == "/");

        var active = isActive ? "active" : "";
        var displayWithTabItem = true;
        return (
          <li className={`nav-item   ${active} ${displayWithTabItem} `}>
            <Link
              className="nav-link"
              to={link.to + `?key=${link.key}`}
              style={{ padding: "0.75rem" }}
            >
              <i className={`fas fa-fw ${link.icon} `}></i>
              <span>{link.name}</span>
            </Link>
          </li>
        );
      });
    }
    return result;
  }

  function menuLink_1(title, link, index) {
    var disableHeading = "show";
    return (
      <React.Fragment>
        <div className={`sidebar-heading ${disableHeading}`} key={index}>
          {title}
        </div>

        {menuLink_2(link)}

        {/* <hr class={`sidebar-divider ${disableHeading}`} /> */}
      </React.Fragment>
    );
  }

  function showMenus(menu) {
    var result = null;
    if (menu.length > 0) {
      result = menu.map((menu, index) => {
        return menuLink_1(menu.title, menu.link, index);
      });
    }
    return result;
  }

  return (
    <div className="col-2 col-2-nav">
      <ul
        className="navbar-nav navbar-main sidebar sidebar-dark accordion"
        id="accordionSidebar"
        style={{ overflowX: "hidden", backgroundColor: "rgb(193, 32, 38)" }}
      >
        <a
          className="sidebar-brand d-flex align-items-center justify-content-center"
          href="#"
        >
          <div className="sidebar-brand-icon ">
            <img
              width="80%"
              src="https://adminlte.io/cdn-cgi/image/width=160,height=27,quality=80,format=auto,onerror=redirect,metadata=none/cdn-cgi/image/quality=80,format=auto,onerror=redirect,metadata=none/wp-content/uploads/2021/03/logo.png"
              className="img-responsive"
              alt="Image"
            />
          </div>
          <div className="sidebar-brand-text">
            <img
              width="80%"
              src="https://adminlte.io/cdn-cgi/image/width=160,height=27,quality=80,format=auto,onerror=redirect,metadata=none/cdn-cgi/image/quality=80,format=auto,onerror=redirect,metadata=none/wp-content/uploads/2021/03/logo.png"
              className="img-responsive"
              alt="Image"
            />
          </div>
        </a>
        {showMenus(menu)}
      </ul>
    </div>
  );
}
